import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';
import React from 'react';
import InfoWindowEx from './InfoWindowEx'
import forest from '../img/forest.png';
import one_1 from "../img/one_1.jpg";

import one_colum_0 from "../img/one_colum.jpg";

import { Div, Button } from '@vkontakte/vkui';
import MyIcon from '../img/map.png'
import connect from '@vkontakte/vkui-connect';

export class MapContainer extends React.Component {


  constructor(props) {
    super(props);
   this.state = {
    nameButton: "Я на месте",
    showingInfoWindow: false,
    activeMarker: {},
    selectedPlace: {},

  };
 }


  onMarkerClick = (props, marker, e) =>{
  switch(marker.id_marker){
     
    case 'one_2':
   
 this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });

    break;
    case 'I':
this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });

    break;
    case 'one_1':
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });
    break;
    default:
    console.log("ошибочка");
    break;

  }}
   submit = (e) => {
    console.log(this.state.selectedPlace);
  this.props.checkGeo(this.state.selectedPlace);


   }

   
    
  onMapClicked = (props) => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      })
    }
  };
 

  render() {
       const style = {
      width: '100%',
      height: '100%',
      'marginLeft': 'auto',
      'marginRight': 'auto'
    }
    return (
      <Map google={this.props.google}
      style={style}
      zoom={16}
       initialCenter={{
            lat: this.props.geo.lat,
            lng: this.props.geo.long
        }}
		gestureHandling={'greedy'}
    streetViewControl={false}
    mapTypeControl={false}
    zoomControl={false}
          onClick={this.onMapClicked}>
<div align="center" style={{"marginTop":"100%"}}>
    
 </div>     

         <Marker onClick={this.onMarkerClick}
              position={{ lat: 59.934777, lng: 30.302896 }}
              name={"Колонны славы"}
              img={one_1}
              img_1={one_colum_0}
            
              pretitle={"Колонны со статуями Славы в Санкт-Петербурге. Памятник в честь побед Лейб-гвардии Конного полка в Отечественной войне 1812 года был установлен на Конногвардейском бульваре в 1845-1846 годах. Автор статуй — Х. Д. Раух, автор колонн — К. И. Росси."}
              id_marker={"one_1"}
              text={<div style={{"fontWeight":"bold","fontSize":"120%"}}><p align="center">Приготовьтесь, сейчас вы узнате 3 самых важных и интересных фактов об этом месте. И мы начинаем!:))</p><p>Колонны Славы выполнены в ионическом ордере из серого сердобольского гранита.<br/>Летящие статуи богини Ники с лавровыми венками в руках,наверху, были изготовлены в Берлине по проекту скульптора Х. Д. Рауха и подарены императору Николаю I королем Фридрихом Вильгельмом IV в 1845 году в знак ответной благодарностью за копии скульптурных групп «Укрощение коня» П. К. Клодта (оригиналы которых установлены на Аничковом мосту).</p> </div> }
              end={<div style={{"fontWeight":"bold","fontSize":"120%"}}><p>Третий факт: Высота каждой колонны — 12 метров, высота статуи — 2,5 метра.</p></div>}
              pollQuestions={"Как вы думаете какой высоты эти колонны?"}
              answer_1={"12 метров"}
              answer_2={"13 метров"}
              answer_3={"15 метров"}
              but={<Button level={"commerce"} onClick={this.submit}>{this.state.nameButton}</Button>}
                 />
              


         <Marker onClick={this.onMarkerClick}
                pretitle={'Находится не известно что... Придите и узнайте'}
                img={forest}
                name={'Первая'}
                 end={<div style={{"fontWeight":"bold","fontSize":"120%"}}><p>Третий факт: Высота каждой колонны — 12 метров, высота статуи — 2,5 метра.</p></div>}
                 pollQuestions={"Как вы думаете какой высоты эти колонны?"}
                text={<div style={{"fontWeight":"bold","fontSize":"120%"}}><p align="center">Приготовьтесь, сейчас вы узнате 3 самых важных и интересных фактов об этом месте. И мы начинаем!:))</p><p>Колонны Славы выполнены в ионическом ордере из серого сердобольского гранита.<br/>Летящие статуи богини Ники с лавровыми венками в руках,наверху, были изготовлены в Берлине по проекту скульптора Х. Д. Рауха и подарены императору Николаю I королем Фридрихом Вильгельмом IV в 1845 году в знак ответной благодарностью за копии скульптурных групп «Укрощение коня» П. К. Клодта (оригиналы которых установлены на Аничковом мосту).</p> </div> }
                pollQuestions={"Как вы думаете почему она первая?"}
                answer_1={"Здесья кто-то живет"}
                answer_2={"Её создали первой"}
                answer_3={"Первое место в городе"}
                id_marker={"one_2"}
               
                but={<Button level={"commerce"} onClick={this.submit}>{this.state.nameButton}</Button>}
                position={{lat: 59.934239, lng: 30.303854}}
                 />



                   <Marker onClick={this.onMarkerClick}
                position={{ lat: this.props.geo.lat, lng: this.props.geo.long }}
                id_marker={"I"}
                icon={{
      url:MyIcon,
      anchor: new this.props.google.maps.Point(32,32),
      scaledSize: new this.props.google.maps.Size(32,51)
    }} 
                name={'Это вы'} />


        <InfoWindowEx
          marker={this.state.activeMarker}
          visible={this.state.showingInfoWindow}>
            <Div align="center">
            <img src={this.state.selectedPlace.img} style={{"width":"90%","height":"90%"}}/>
            <h1>{this.state.selectedPlace.name}</h1>
              <p>{this.state.selectedPlace.pretitle}</p>
              {this.props.errorGeo}
               {this.state.selectedPlace.but}
              </Div>
            
        </InfoWindowEx> 
      </Map>
    );
  }
}
 
export default GoogleApiWrapper({
  apiKey: ('AIzaSyCL0U2diIKJtBb5gmgyUsRvpSIIKIfKCv0')
})(MapContainer)