import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, ScreenSpinner, Epic, PanelHeader, Panel, Tabbar, TabbarItem, Avatar, Div, HeaderButton, List, Cell, Snackbar} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Polls from 'react-polls';




class Poll extends React.Component {
constructor (props) {
super(props);
const nul = 0;
let n1 = 0;
let n2 = 1;
let n3 = 2;
const TB = this.props.Poll;
const pollQuestion = this.props.selectedPlace.pollQuestions;
const pollAnswers = [
  { option: TB === "" ? this.props.selectedPlace.answer_1 : this.props.Poll[n1].option, votes: TB === "" ? nul : this.props.Poll[n1].votes },
  { option: TB === "" ? this.props.selectedPlace.answer_2 : this.props.Poll[n2].option, votes: TB === "" ? nul : this.props.Poll[n2].votes },
  { option: TB === "" ? this.props.selectedPlace.answer_3 : this.props.Poll[n3].option, votes: TB === "" ? nul : this.props.Poll[n3].votes }

];

this.state = {
    pollAnswers: [...pollAnswers],
    pollQuestion:pollQuestion,
    selectedPlace:this.props.selectedPlace
  }
 }
  // Handling user vote
  // Increments the votes count of answer when the user votes
  handleVote = (voteAnswer) => {
    const  pollAnswers = this.state.pollAnswers
    const newPollAnswers = pollAnswers.map(answer => {
      if (answer.option === voteAnswer) answer.votes++
      return answer
    });
    this.setState({pollAnswers: newPollAnswers});
  
   const op = JSON.stringify(newPollAnswers);
   this.props.setPoll(op);
  
  this.props.setEnd(true);
  }

	render(){

		 const { pollAnswers } = this.state.pollAnswers
	 return(
	 	<Div>
	 	<Polls theme="purple" question={this.state.pollQuestion} answers={this.state.pollAnswers} onVote={this.handleVote} />
        </Div>
	 	);
	}
}

export default Poll;