import React from 'react';
import connect from '@vkontakte/vkui-connect';
import {  View, ScreenSpinner, Epic, Button,PanelHeader, Panel, Tabbar, TabbarItem, Avatar, Div, HeaderButton, Cell, Snackbar, Gallery, Group} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Profile from './panels/Profile.js'
import MapContainer from './panels/MapContainer';
import End from './panels/End'
import Poll from './panels/Poll';
import Begin from './panels/Begin'
import Questions from './panels/Questions';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28Messages from '@vkontakte/icons/dist/28/messages';
import Icon28Search from '@vkontakte/icons/dist/28/search';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon16Done from '@vkontakte/icons/dist/16/done';

const orangeBackground = {
  backgroundImage: 'linear-gradient(135deg, #ffb73d, #ffa000)'
};

const blueBackground = {
  backgroundColor: 'var(--accent)'
};
class App extends React.Component {
  constructor (props) {
    super(props);
  

this.so = <p style={{color: 'red'}}>Попробуйте еще раз</p>;
    this.num = 0;
    
    this.state = {
      activeStory: null,
      activePanel: "map",
      quests:null,
      header: true,
      snackbar: null,
      popout: null,
      selectedPlace:null,
      fetchedUser: null,
      access_token: null,
      geoData: null,
      errorGeo:null,
      Poll:null,
      showEnd:false
    
      
    };
    this.onStoryChange = this.onStoryChange.bind(this);
    this.onToChange
     = this.onToChange.bind(this);
  
  }


  componentDidMount() {   //приходят события vk connect
    connect.subscribe((e) => {
      switch (e.detail.type) {
        case 'VKWebAppGetUserInfoResult':
         
           this.setState({fetchedUser: e.detail.data});
           this.setState({activeStory: "profile"})
           console.log(e.detail.data.id);
        break;

          case 'VKWebAppGeodataResult':
          if(e.detail.data.available === true){
          this.setState({geoData: e.detail.data})
        }else{
          console.log("все плохо");
        }
          

           
          break;

          case 'VKWebAppAccessTokenReceived':
             this.setState({ access_token: e.detail.data.access_token})
          break;

          case 'VKWebAppCallAPIMethodResult':
            switch(e.detail.data.request_id){
    

             case "getAll":
            // let arr = JSON.parse(e.detail.data.response[1].value);
              this.setState({quests:e.detail.data.response});
              console.log(JSON.parse(e.detail.data.response[1].value).name);
               this.setState({ activeStory: 'questions' });
               
             break;

             case 'getAllQuests':
             
              connect.send("VKWebAppCallAPIMethod", {"method": "storage.get", "request_id": "getAll", "params": {"keys":e.detail.data.response,"user_id": this.state.fetchedUser.id, "v":"5.101", "access_token": '24712b3f24712b3f24712b3f9e241c30172247124712b3f79ff04100cce4d49eb8df420'}});
                console.log(e.detail.data.response);
                
             break;

             case 'getPoll':
            console.log(e.detail)
             if(e.detail.data.response === ""){
              this.setState({Poll: ""});
        this.setState({activePanel:'questions', popout:null});
      }
              else{ 
             const o = JSON.parse(e.detail.data.response);
             this.setState({Poll: o})
             this.setState({activePanel:'questions', popout:null})
             console.log(o); }
           
           
             break;

             case 'setPoll':
               console.log(e.detail.data.response);
             break;
          }
          break;
        
        default:
        console.log(e.detail.data)  ;
      }
    });
    connect.send('VKWebAppGetUserInfo', {});
    connect.send("VKWebAppGetAuthToken", {"app_id":7150376, "scope": "friends"});
    connect.send("VKWebAppGetGeodata", {});
   
  }




   


  onClick =(e) => {
    connect.send("VKWebAppGetGeodata", {});
   
    this.setState({ popout: <ScreenSpinner /> });
     this.setState({ activeStory:'map'});
    setTimeout(() => { this.setState({ popout: null }) }, 2000);  
  }
  
  setPoll =(answer, e)=>{
connect.send("VKWebAppCallAPIMethod", {"method": "storage.set", "request_id": "setPoll", "params": 
        {"key": this.state.selectedPlace.id_marker+"Poll" , "value": answer, "v":"5.101", "access_token": this.state.access_token}});   


}


theEnd=(e)=>{
let arr = {
  name:this.state.selectedPlace.name,
  img:this.state.selectedPlace.img,
  num: 100};
//let json = JSON.stringify(arr);
        connect.send("VKWebAppCallAPIMethod", {"method": "storage.set", "request_id": "theEnd", "params": 
        {"key": this.state.selectedPlace.id_marker, "value":arr ,"user_id": this.state.fetchedUser.id, "v":"5.101", "access_token": '24712b3f24712b3f24712b3f9e241c30172247124712b3f79ff04100cce4d49eb8df420'}});
this.setState({activePanel:'map'});
      }
 

  setEnd= (q, e) =>{this.setState({showEnd:q})}


  GetVar = (e)=>{
    connect.send("VKWebAppCallAPIMethod", {"method": "storage.getKeys", "request_id": "getAllQuests", "params": 
        {"user_id": this.state.fetchedUser.id, "v":"5.101", "access_token": '24712b3f24712b3f24712b3f9e241c30172247124712b3f79ff04100cce4d49eb8df420'}});

  
  }
  checkGeo = (a, e) =>{
  connect.send("VKWebAppGetGeodata", {});
    this.setState({popout:<ScreenSpinner />});
    if((this.state.geoData.lat).toFixed(this.num) === (a.position.lat).toFixed(this.num) && 
      (this.state.geoData.long).toFixed(this.num) === (a.position.lng).toFixed(this.num)){
      this.setState({errorGeo: null});
  let arr = {
    name:a.name,
    img:a.img,
    num: 50};
//  let json = JSON.stringify(arr);
      connect.send("VKWebAppCallAPIMethod", {"method": "storage.set", "request_id": "setQuests", "params": 
        {"key": a.id_marker , "value": arr,"user_id": this.state.fetchedUser.id, "v":"5.101", "access_token": '24712b3f24712b3f24712b3f9e241c30172247124712b3f79ff04100cce4d49eb8df420'}});
    
     connect.send("VKWebAppCallAPIMethod", {"method": "storage.get", "request_id": "getPoll", "params": {"key": a.id_marker+"Poll", "v": "5.101", "access_token": this.state.access_token}});
      
     
       

      this.setState({selectedPlace: a});
     
       
       
    }
    else{
   this.setState({errorGeo:this.so , popout:null});
    
    }
  }


  onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story });
  }

  onToChange =(e)=> {
     this.setState({ activePanel: e.currentTarget.dataset.to});

  }

  render =()=> {
    return (
      <Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'profile'}
            data-story="profile"
            text="Профиль"
          ><Icon28Newsfeed /></TabbarItem>
          <TabbarItem
            onClick={this.onClick}
            selected={this.state.activeStory === 'map'}
            data-story="map"
            text="Карта"
          ><Icon28Search /></TabbarItem>
          <TabbarItem
            onClick={this.GetVar}
            selected={this.state.activeStory === 'questions'}
            data-story="questions"
            label="5"
            text="Задания"
          ><Icon28Messages /></TabbarItem>
  
        </Tabbar>
      }>

      
        <View id="profile" activePanel="profile">
          <Panel id="profile">
            <PanelHeader>Профиль</PanelHeader>
        
           <Profile fetchedUser={this.state.fetchedUser}/>
          </Panel>
        </View>

       
        <View id="map" popout={this.state.popout}  activePanel={this.state.activePanel}>
          <Panel id="map">
            <PanelHeader>Карта</PanelHeader>
            <MapContainer geo={this.state.geoData} checkGeo={this.checkGeo} errorGeo={this.state.errorGeo}/>
         
          </Panel>
          <Panel id="questions">
           {this.state.selectedPlace &&
           <PanelHeader
			left={<HeaderButton onClick={this.onToChange} data-to="map">
				<Icon24Back/></HeaderButton>}>{this.state.selectedPlace.name}</PanelHeader>}
			      <Begin selectedPlace={this.state.selectedPlace} />     
            <Poll selectedPlace={this.state.selectedPlace} setPoll={this.setPoll} Poll={this.state.Poll} setEnd={this.setEnd}/>
         <Div>
{this.state.showEnd && <End theEnd={this.theEnd} selectedPlace={this.state.selectedPlace} />}
         </Div>
          </Panel>

        </View>

       

        <View id="questions" activePanel="quest">
          <Panel id="quest">
            <PanelHeader>Задания</PanelHeader>
          <Questions quests={this.state.quests}/>
          </Panel>

        </View>
 
      </Epic>)
  }
}

export default App;
